.overview{
    opacity: 0;
    animation:  appear 0.8s 0.2s ease-out forwards;
}

@keyframes appear {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1.0;
    }
}

.pod{
    transform-origin: center;
    animation: expand 0.8s ease-out forwards;
}

.activerect{
    animation: appear 0.8s ease-out forwards;
}

.hiddenrect{
    opacity: 0;
}

.podcircle{
    fill: #e8e8e8;
    stroke:#3a7bd4;
    stroke-width:1px;
}

.activepodcircle{
    animation: colourchange 0.8s ease-out forwards;
}

.containercircle{
    fill: transparent;
}
.containercircleactive{
    animation:  circlecolourchange 0.8s ease-out forwards;
}

@keyframes circlecolourchange {
    0% {
        fill: transparent
    }
    100% {
        fill: #3A7BD4;
    }
}

@keyframes colourchange {
    0% {
        fill: #e8e8e8;
        stroke:#3a7bd4;
        
    }
    100% {
        fill: #3A7BD4;
        stroke:#E8E8E8;
        stroke-width:5px;
    }
}

@keyframes expand {
    0% {
        transform: scale(1.0);   
    }
    100% { 
        transform: translate(-700px,270px) scale(4.5) 
    }
}


.panelcontainer {
    transform-origin: center;
    animation: shrinkpanel 0.8s ease-out forwards;
}

@keyframes shrinkpanel {
    0% {
        transform: scale(1.0);
        
    }
    100% {
        transform: translate(50px,-50px) scale(0.75); 
    }
}

