.activerect{
    animation: appear 0.8s ease-out forwards;

}

.hiddenrect{
    opacity: 0;
}

.overviewrect{
    animation:  appear 0.8s ease-out forwards;
}
.workeruiactive1{
    animation:  move1 0.8s  ease-out forwards;
}

.workeruiactive2{
    animation:  move2 0.8s  ease-out forwards;
}

.workeruiactive3{
    animation:  move3 0.8s  ease-out forwards;
}

.workerui{
    transform: translate(0px,0px);
}

.workertext {
    fill: #66676a;   
}

.workertextactive {
    animation:  movelabel 0.8s  ease-out forwards;
}

.description {
    opacity: 0;
    animation: appear 0.8s 0.8s ease-out forwards;
}

.descriptiontext{
    fill: white;
    font-size: 12px;
}

@keyframes appear {
    0% {
        opacity: 0;
    }
    100% {
       opacity: 1.0;
    }
}

@keyframes move1 {
    0% {
        transform: translate(0px,0px);
    }
    100% {
        transform: translate(60px,-220px);
    }
}

@keyframes move2 {
    0% {
        transform: translate(0px,0px);
    }
    100% {
        transform: translate(80px,-180px);
    }
}

@keyframes move3 {
    0% {
        transform: translate(0px,0px);
    }
    100% {
        transform: translate(50px,-130px);
    }
}

@keyframes movelabel {
    0% {
        transform: translate(0px,0px);
        fill: #66676a;  
    }
    100% {
        fill: white; 
        transform: translate(0px,80px);
    }
}


