.thumbnail {
    box-shadow: 0 1px 2px rgba(0,0,0,0.07);
    position: relative;
    clear: left;
    background: #fff;
    border-radius: 0px;
    margin: 10px;
}

.thumbnailactive{
    
    position: absolute;
    clear: left;
    background: #fff;
    margin: 10px;
    top: 10;
    left: 10;
    z-index: 2;   
}

.thumbnailVideo{
    box-shadow: 0 1px 2px rgba(0,0,0,0.07);
    position: relative;
    clear: left;
    background: #fff;
    border-radius: 2px;
    width:320px;
    margin: 10px;
}


.imagecontainer{
    overflow: hidden;
    flex: 1;
}

.image{
    width:300px;
    max-width: 100%;
   
}

.imageactive{
    animation: expand 0.5s ease-out forwards;
}

@keyframes expand {
    0% {
        width: 300px;
    }
    100% {
       width: 750px
    }
}


.info{
    font-size: 0.8em;
    color: rgba(50,53,68,0.4);
    position: absolute;
    bottom: 5px;
    left: 10px;
}

.gallery{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    height: calc(100vh - 116px);
    overflow-y: auto;
    min-width: 720px;
    align-content: baseline;
}

.opaque{
    position: absolute;
    height: calc(100vh - 116px);
    width: calc(100vw - 340px);
    background: lightgrey;
    opacity: 0.95;
    z-index: 1;
}

@keyframes drawer {
    0% {
        transform: translate(-200px, 0px);
        opacity: 0;
    }
    100% {
        transform: translate(0px, 0px);
        opacity: 1;
    }
}

@keyframes tab {
    0% {
        transform: translate(0px,40px);
    }
    100% {
        transform: translate(0px,0px);
    }
}

@keyframes darken{
    0% {
        box-shadow: none;
    }
    100% {
        box-shadow: -6px 5px 10px 3px rgba(0,0,0,0.09);
    }
}

.overview {
    position: relative;
    transform: translate(-200px, 0px);
    opacity: 0;
    background:#3a7bd4;
    z-index: 2;
    width:0px;
    width: 200px;
    animation: drawer 0.5s 0.6s ease-out forwards;
    border-radius: 0px 3px 3px 0px;
    padding: 20px 10px 10px 10px;
    box-sizing: border-box;
    font-size: 0.75em;
    color: white;
    line-height: 18px;
}

.tabtitle {
    position: absolute;
    background:white;
    height: 40px;
    width: 200px;
    transform: translate(0px,40px);
    z-index: 2;
    border-radius: 5px 5px 0px 0px;
    animation: 
        tab 0.5s 0.6s ease-out forwards;
       
        
    padding: 7px 0px 0px 0px;
    font-size: 0.7em;
    font-family: Futura,Trebuchet MS,Arial,sans-serif;
   box-shadow:none;
    text-align: center;
    font-weight: 700;
}

.fullcontainer {
    position: absolute;
    z-index: 2;
    background: transparent;
    animation: darken 0.5s 1.6s ease-out forwards;
}




