.overviewrect{
    animation:  appear 0.8s ease-out forwards;
}
.createuiactive{
    animation:  move 0.8s  ease-out forwards;
}

.createui{
    transform: translate(0px,0px);
}

.createtext {
    fill: #66676a;   
}

.createtextactive {
    animation:  movelabel 0.8s  ease-out forwards;
}

.description {
    opacity: 0;
    animation: appear 0.8s 0.8s ease-out forwards;
}

.descriptiontext{
    fill: white;
    font-size: 12px;
}

@keyframes appear {
    0% {
        opacity: 0;
    }
    100% {
       opacity: 1.0;
    }
}

@keyframes move {
    0% {
        transform: translate(0px,0px);
    }
    100% {
        transform: translate(93px,25px);
    }
}

@keyframes movelabel {
    0% {
        transform: translate(0px,0px);
        fill: #66676a;  
    }
    100% {
        fill: white; 
        transform: translate(0px,80px);
    }
}


