.summary {
    background: #323544;
	height: calc(100vh - 230px);
    width: 240px;
    min-height: 473px;
}

.contentcontainer {
    position: absolute;
    left: 240px;
    top: 0px;
    width: calc(100vw - 240px);
    height: 100vh;
    padding: 0px 30px 30px 30px;
    min-width: 654px;
    min-height: 700px;
}

.back {
    font-size: 50px;
    color: white;
    padding: 10px;
    text-decoration: none;
}

.tech {
    color:#E25B26;
    font-weight: 400;
}
.menu {
    height: 56px;
    background:#E25B26;
    color: white;
    margin:20px;
    min-width: 510px;
}

.menu nav {
	float: right;
	font-size: 1em;
}

.menu nav a {
	display: inline-block;
	height: 56px;
	line-height: 56px;
	padding: 0 15px;
	min-width: 50px;
	text-align: center;
	background: rgba(255,255,255,0);
	text-decoration: none;
	color: #FFF;
	will-change: background-color;
}

.menu nav a:hover,
.menu nav a:active {
	background: rgba(0,0,0,0.2);
}

.menu nav a.active {
	background: #323544;
}
.content {
    padding: 0px 20px 20px 20px;
}

.linkbox {
   
    background: rgba(50,53,68,0.9);
    color: white;
    
    height: 230px;
    font-size:0.9em;
    font-family: Futura,Trebuchet MS,Arial,sans-serif;
    
    padding: 7px 20px 20px 20px;
    width: 240px;
}

.maincopy{
    margin-top: 30px;
    font-size: 20px;
    line-height: 30px;
}

.footergraphic{
    position: absolute;
    right: -50px;
    bottom: -10px;
}

.title{
    text-align: center;
    font-family: Futura,Trebuchet MS,Arial,sans-serif;
    font-size: 2em;
    color: white;
    text-transform: uppercase;
    margin-bottom: 0px;
    padding: 10px 10px 0px 10px;
}

.web{
    font-family: Futura,Trebuchet MS,Arial,sans-serif;
    font-size: 0.9em;
    color: white;
    padding: 0px;
    margin-top: 7px;
}

.weblabel{
    font-family: Futura,Trebuchet MS,Arial,sans-serif;
    font-size: 0.9em;
    
    color: lightgrey;
    padding: 0px;
    margin-top: 20px;
}

.projectpic{
    padding: 30px 35px 65px 35px;
}

.link{
    color: white;
}

.datecontainer {
    font-family: Futura,Trebuchet MS,Arial,sans-serif;
    color: white;
    text-transform: uppercase;
    font-size: 1.2em;
    background: #323544;
    width: 230px;
    height: 50px;
}

.date{
    text-align: center;
    color: lightgrey;
}


