	margin: 0;
	background: #FAFAFA;
	
	font-family:'GillSans', 'Gill Sans', sans-serif;
	font-weight: 400;
	color: #444;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
    overflow: hidden;
}

* {
	box-sizing: border-box;
}

#app {
	height: 100%;
}



