.hexcontainer{
   
}

.hexcontaineractive{
    transform-origin: center;
    animation: move 0.8s ease-out forwards;
}

.route{
    
}

.routeactive{
    transform-origin: center;
    animation: movecontainer 0.8s ease-out forwards;
}


@keyframes movecontainer {
    0% {
        transform: scale(1);
    }
    100% {
       transform: translate(65px,10px) scale(1.25);
       
    }
}

@keyframes move {
    0% {
        transform: scale(1);
    }
    100% {
       transform: translate(40px,0px) scale(0.75);
       
    }
}

.hexactive{
    fill: white;
}

.hex{
    fill: none;
}


